<template>
  <div class="home bg-first">
    <div>
      <section>
        <div class="w-90 mx-auto pt-3 mb-1">
          <p>忘記密碼流程</p>
          <p>寄送驗證碼</p>
        </div>
        <div class="d-flex align-items-center position-relative w-100 ">
          <div class="d-flex justify-content-between w-90 mx-auto z-2">
            <div class="circle-num d-flex align-items-center justify-content-center font-weight-bold bg-white">1</div>
            <div class="circle-num d-flex align-items-center justify-content-center font-weight-bold bg-white">2</div>
            <div class="circle-num d-flex align-items-center justify-content-center font-weight-bold bg-white">3</div>
          </div>
          <div class="w-100 line position-absolute z-1"></div>
        </div>
      </section>
      <section class="d-flex justify-content-center my-5">
        <form action="">
          <div class="d-flex p-2 bg-white input-frame">
            <p class="mb-0 mr-1">帳號</p>
            <input type="tel" maxlength="10" v-model="account" placeholder="輸入手機號碼">
          </div>
          <div v-if="alreadysend" class="d-flex mt-2 p-2 bg-white input-frame">
            <p class="mb-0 mr-1">驗證碼</p>
            <input type="text" v-model="code">
          </div>
          <div v-if="!alreadysend" class="mt-3 mx-auto py-3 px-5 bg-second w-fit text-white font-weight-bold login-btn mb-3" @click="sendcode">發送驗證碼</div>
          <div v-if="alreadysend">
            <div class="d-flex mx-auto">
              <div class="mt-3 py-3 px-2 bg-second w-fit text-white font-weight-bold login-btn mb-3 h6 mr-3">倒數{{sencond}}秒</div>
              <div class="mt-3 py-3 px-2 bg-second w-fit text-white font-weight-bold login-btn mb-3 h6" @click="renew" @>重新發送驗證碼</div>
            </div>
              <div class="mt-2 mx-auto py-3 px-5 bg-second w-fit text-white font-weight-bold login-btn mb-3 h6" @click="submitcode">送出驗證碼</div>
          </div>
        </form>
      </section>
      <section class="pt-5">
        <p class="mb-1 mx-auto text-white bg-second w-fit px-1" @click="$router.push({name: 'Home'})"><u>返回登入首頁</u></p>
        <p class="text-white mx-auto bg-second w-fit px-1 text-center"><u>帳號異常<br>聯繫UUPON客服</u></p>
      </section>
    </div>
    <div v-if="successmodal" class="modal-frame d-flex justify-cintent-center align-items-center">
      <div class="modal-box bg-white w-85 mx-auto">
        <div class="text-right modal-box-header p-2">
          <p  @click="closemodal">X</p>
        </div>
        <div class="p-3 text-center">
          <p class="h6 font-weight-bold">{{successmodaltext}}</p>
          <div class="mt-4 w-50 border mx-auto py-1" @click="closemodal">OK</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import $ from 'jquery'
export default {
  name: 'Home',
  data () {
    return {
      code: '',
      account: '',
      newaccount: '',
      newpsw: '',
      newpsw2: '',
      detail: false,
      alreadysend: false,
      successmodal: false,
      sencond: 120,
      successmodaltext: '驗證成功',
      time: null
    }
  },
  created () {
  },
  methods: {
    closemodal () {
      this.successmodal = false
      this.$router.push({name: 'Home'})
    },
    sendcode () {
      if (this.account !== '') {
        this.alreadysend = true
        this.time = setInterval(() => {
          if (this.sencond < 1) {
            clearInterval(this.time)
          } else {
            this.sencond -= 1
          }
        }, 1000)
      }
    },
    renew () {
      console.log('重新發送驗證碼')
      clearInterval(this.time)
      this.sencond = 120
      this.sendcode()
    },
    submitcode () {
      if (this.code !== '') {
        console.log('已送出驗證碼')
        this.successmodal = true
      }
    }
  }
}
</script>

<style lang="scss">
.home{
  height: 100vh;
}
.line{
  height: 2px;
  background: #3b3b3b;
}
.circle-num{
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 1px solid #3b3b3b;
}
form input{
  border: none;
  background: transparent;
  outline: none;
}
.input-frame{
  border: 1px solid #000000;
  border-radius: 5px;
}
.login-btn{
  box-shadow: 3px 3px 3px #818181;
}
.modal-frame{
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  background: rgba($color: #000000, $alpha: 0.4);
  z-index: 99;
}
.modal-box{
  border: 1px solid #757575;
}
.modal-box-green{
  border: 2px dashed #7bb38d;
  border-radius: 15px;
  box-sizing: content-box;
}
.modal-box-header{
  border-bottom: 1px solid #757575;
}
</style>



