<template>
  <div class="home bg-first">
    <div>
      <section>
        <div v-if="exist" class="w-90 mx-auto pt-3 mb-1">
          <p>綁定UUPON會員</p>
          <!-- <p>step1. 輸入帳號密碼</p> -->
        </div>
        <div v-else class="w-90 mx-auto pt-3 mb-1">
          <p>快速註冊</p>
          <p>成為UUPON新會員</p>
        </div>
        <!-- <div class="d-flex align-items-center position-relative w-100 ">
          <div class="d-flex justify-content-between w-90 mx-auto z-2">
            <div class="circle-num d-flex align-items-center justify-content-center font-weight-bold bg-white">1</div>
            <div class="circle-num d-flex align-items-center justify-content-center font-weight-bold bg-white">2</div>
            <div class="circle-num d-flex align-items-center justify-content-center font-weight-bold bg-white">3</div>
          </div>
          <div class="w-100 line position-absolute z-1"></div>
        </div> -->
      </section>
      <section class="d-flex justify-content-center mt-5">
        <form v-if="exist" action="">
          <div class="d-flex p-2 bg-white input-frame">
            <p class="mb-0 mr-1">帳號</p>
            <p class="text-gray">{{account}}</p>
          </div>
          <div class="d-flex mt-3 p-2 bg-white input-frame">
            <p class="mb-0 mr-1">密碼</p>
            <input v-if="seepsw" type="text" v-model="psw">
            <input v-if="!seepsw" type="password" v-model="psw">
            <img v-if="seepsw"  src="../assets/img/eye.png" @click="seepsw = !seepsw" width="20px" alt="">
            <img v-if="!seepsw" src="../assets/img/private.png" @click="seepsw = !seepsw" width="20px" alt="">
          </div>
        </form>
        <form v-else action="">
          <div class="d-flex p-2 bg-white input-frame">
            <p class="mb-0 mr-1">帳號</p>
            <p class="text-gray">{{newaccount}}</p>
          </div>
          <div class="d-flex justify-content-between mt-3 p-2 bg-white input-frame">
            <div class="d-flex">
              <p class="mb-0 mr-1">密碼</p>
              <input v-if="seenewpsw" type="text" v-model="newpsw">
              <input v-if="!seenewpsw" type="password" v-model="newpsw">
            </div>
            <img v-if="seenewpsw"  src="../assets/img/eye.png" @click="seenewpsw = !seenewpsw" width="20px" alt="">
            <img v-if="!seenewpsw" src="../assets/img/private.png" @click="seenewpsw = !seenewpsw" width="20px" alt="">
          </div>
          <div class="d-flex justify-content-between mt-3 p-2 bg-white input-frame">
            <div class="d-flex">
              <p class="mb-0 mr-1">再次輸入密碼</p>
              <input v-if="seenewpsw2" type="text" v-model="newpsw2">
              <input v-if="!seenewpsw2" type="password" v-model="newpsw2">
            </div>
            <img v-if="seenewpsw2"  src="../assets/img/eye.png" @click="seenewpsw2 = !seenewpsw2" width="20px" alt="">
            <img v-if="!seenewpsw2" src="../assets/img/private.png" @click="seenewpsw2 = !seenewpsw2" width="20px" alt="">
          </div>
          <p v-if="wrongpsw" class="text-center text-danger mt-2"><u>密碼不一致</u></p>
          <div class="d-flex mt-4 align-items-center justify-content-center">
            <input type="checkbox" v-model="checkbox">
            <p class="mb-0 ml-1 text-white bg-second">同意UUPON<u @click="detail = true" class="h3">會員條款&使用者隱私權條款</u></p>
          </div>
          <p v-if="checkboxnotice" class="text-center text-danger mt-2 h3">*請先勾選同意</p>
          <div class="mt-4 mx-auto py-3 px-5 bg-second text-white w-fit font-weight-bold login-btn mb-3" @click="submit">送出</div>
        </form>
      </section>
      <section>
        <div class="text-center text-danger mt-2" v-if="errorpsw">
          <p>【輸入密碼錯誤】</p>
          <p class="">請重新確認密碼或到UUPON重設密碼</p>
          <p class="h3">忘記密碼請聯繫UUPON客服</p>
        </div>
      </section>
      <section v-if="exist" class="">
        <div class="mx-auto py-3 px-5 bg-second w-fit text-white font-weight-bold login-btn mt-5" @click="login">確認登入</div>
        <!-- <p class="mb-1 mx-auto text-white bg-second w-fit px-1" @click="$router.push({name: 'Forgetpsw'})"><u>忘記密碼</u></p> -->
        <!-- <p class="text-white mx-auto bg-second w-fit px-1 text-center"><u>帳號異常<br>聯繫UUPON客服</u></p> -->
      </section>
    </div>
    <div v-if="successmodal" class="modal-frame d-flex justify-cintent-center align-items-center">
      <div class="modal-box bg-white w-85 mx-auto">
        <div class="text-center modal-box-header p-2">
          <p class="h7 mb-0">恭喜! 僅差最後一步</p>
        </div>
        <div class="p-3 text-center">
          <p class="h7">同意授權UUPON會員資料給台北通APP</p>
          <div class="mt-4 d-flex justify-content-around mx-auto py-1">
            <div class="w-40 border py-2 mr-2" @click="closemodal({type: 'disagree'})">不同意</div>
            <div class="w-40 border py-2" @click="closemodal({type: 'agree'})">同意</div>
          </div>
          <!-- <div class="mt-4 w-50 border mx-auto py-1" @click="uccessmodal = false">OK</div> -->
        </div>
      </div>
    </div>
    <div v-if="detail" class="modal-frame d-flex justify-cintent-center align-items-center">
      <div class="modal-box-green bg-green w-90 mx-auto">
        <div class="p-3 text-center">
          <p class="w-fit mx-auto bg-second h6">UUPON會員條款</p>
          <p>qwewfgvewgbvrsrfbh</p>
          <p>qwewfgvewgbvrsrfbh</p>
          <p>qwewfgvewgbvrsrfbh</p>
          <p>qwewfgvewgbvrsrfbh</p>
          <div class="mt-4 w-fit border mx-auto px-2 py-1 bg-white" @click="detail = false">已閱讀完畢</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import $ from 'jquery'
export default {
  name: 'Home',
  data () {
    return {
      psw: '',
      account: '',
      newaccount: '',
      newpsw: '',
      newpsw2: '',
      successmodal: false,
      exist: false,
      checkbox: false,
      detail: false,
      wrongpsw: false,
      errorpsw: false,
      seepsw: false,
      seenewpsw2: false,
      seenewpsw: false,
      checkboxnotice: false
    }
  },
  created () {
    this.account = document.querySelector('#mbNo').value
    this.newaccount = document.querySelector('#mbNo').value
    console.log(document.querySelector('#mbNo').value)
  },
  methods: {
    login () {
      if (this.psw !== '' && this.account !== '') {
        this.successmodal = true
      }
      this.errorpsw = true
    },
    submit () {
      if (this.newpsw2 === '' || this.newpsw === '' || this.newpsw2 !== this.newpsw) {
        this.wrongpsw = true
        return
      }
      if (this.checkbox === false) {
        this.checkboxnotice = true
        return
      }
      if (this.newpsw2 !== '' && this.newpsw !== '' && this.newaccount !== '') {
        this.successmodal = true
      }
    },
    closemodal (item) {
      this.successmodal = false
      switch (item.type) {
        case 'disagree':
          console.log('不同意')
          break
        case 'agree':
          console.log('同意')
          break
        default:
      }
    }
  }
}
</script>

<style lang="scss">
.home{
  height: 100vh;
}
.line{
  height: 2px;
  background: #3b3b3b;
}
.circle-num{
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 1px solid #3b3b3b;
}
form input{
  border: none;
  background: transparent;
  outline: none;
}
.input-frame{
  border: 1px solid #000000;
  border-radius: 5px;
}
.login-btn{
  box-shadow: 3px 3px 3px #818181;
}
.modal-frame{
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  background: rgba($color: #000000, $alpha: 0.3x);
  z-index: 99;
}
.modal-box{
  border: 1px solid #757575;
}
.modal-box-green{
  border: 2px dashed #7bb38d;
  border-radius: 15px;
  box-sizing: content-box;
}
.modal-box-header{
  border-bottom: 1px solid #757575;
}
</style>



