import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home.vue'
import Forgetpsw from '@/views/Forgetpsw.vue'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/forgetpsw',
      name: 'Forgetpsw',
      component: Forgetpsw
    }
  ]
})



